import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Device } from 'app/interfaces/device';
import { Observable } from 'rxjs';
import { SelectedDevice } from 'app/interfaces/selected-device';
import { Slave } from 'app/interfaces/slave';
import { TofParameters, AoaParameters, Configuration } from 'app/interfaces/configuration';
import { SetupCapability } from 'app/interfaces/setup-capability';
import { environment } from '../../environments/environment';



@Injectable({
	providedIn: 'root'
})
export class RestApiService {
	private env = environment;

	private SERVER_IP: string = this.env.server_ip;
	private SERVER_PORT: number = this.env.server_port;

	constructor(private http: HttpClient) { }

	getAppVersion(): Observable<any> {
		return this.http.get<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/appVersion`);
	}

	getConfiguration(): Observable<Configuration> {
		return this.http.get<Configuration>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/configuration`);
	}

	setConfiguration(v: Configuration): Observable<any> {
		return this.http.post<Configuration>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/configuration`, JSON.stringify(v));
	}

	getDeviceList(): Observable<Device[]> {
		return this.http.get<Device[]>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/devices`);
	}

	indentifyDevices(deviceList: Device[]): Observable<Device[]> {
		return this.http.post<Device[]>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/devices`, JSON.stringify(deviceList));
	}

	initializeRtlsUtil(deviceList: Device[], websocket_port: number, rtlsutil_timeout: number): Observable<SelectedDevice[]> {
		return this.http.post<SelectedDevice[]>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`, JSON.stringify({
			'deviceList': deviceList,
			'websocketPort': websocket_port,
			'rtlsUtilTimeout': rtlsutil_timeout
		}));
	}

	finalizeRtlsUtil(): Observable<any> {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`);
	}

	getSetupCapability(): Observable<SetupCapability> {
		return this.http.get<SetupCapability>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`);
	}

	scan(scan_time: Number): Observable<Slave[]> {
		return this.http.post<Slave[]>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/scan`, JSON.stringify({
			'scan_time': scan_time
		}));
	}

	connect(slave: Slave, connection_interval: Number): Observable<any> {
		return this.http.post<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/connect`, JSON.stringify({
			'slave': slave,
			'connection_interval': connection_interval
		}));
	}

	disconnect(): Observable<any> {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/connect`);
	}

	enable_cci(): Observable<any> {
		return this.http.get<Device[]>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cci`);
	}

	disable_cci(): Observable<any> {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cci`);
	}

	enable_tof(tof_parameters: TofParameters): Observable<any> {
		return this.http.post<Device[]>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/tof`, JSON.stringify({ "tof_parameters": tof_parameters }));
	}

	disable_tof(): Observable<any> {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/tof`);
	}

	enable_aoa(aoa_parameters: AoaParameters): Observable<any> {
		return this.http.post<Device[]>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/aoa`, JSON.stringify({ "aoa_parameters": aoa_parameters }));
	}

	disable_aoa(): Observable<any> {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/aoa`);

	}

	shutdown(): Observable<any> {
		return this.http.get(`http://${this.SERVER_IP}:${this.SERVER_PORT}/shutdown`);
	}

}
