import { Component, OnInit, Input, AfterViewInit, OnDestroy, ViewChild } from '@angular/core';
import { ChartDataSets, ChartOptions } from 'chart.js';
import { Label, Color, BaseChartDirective } from 'ng2-charts';
import { ContinuousDataService } from 'app/services/continuous-data.service';
import { SelectedDevice } from 'app/interfaces/selected-device';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { ConfigurationService } from 'app/services/configuration.service';
import { MatDialog } from '@angular/material';
import { ConfigurationDialogComponent } from 'app/dialogs/configuration-dialog/configuration-dialog.component';
import { SampleCounter } from 'app/interfaces/sample-counter';

@Component({
	selector: 'tof-chart',
	templateUrl: './tof-chart.component.html',
	styleUrls: ['./tof-chart.component.scss']
})
export class TofChartComponent implements OnInit, OnDestroy {

	_deviceList: SelectedDevice[];

	@Input('deviceList')
	set deviceList(value: SelectedDevice[]) {
		this._deviceList = value;
		this.initChart();
	}

	@ViewChild(BaseChartDirective, { static: false })
	public chart: BaseChartDirective;


	public lineChartData: ChartDataSets[];
	public lineChartLabels: Label[];
	public lineChartOptions: ChartOptions = {
		responsive: true,
		maintainAspectRatio: false,

		scales: {

			xAxes: [{
				display: false,

				scaleLabel: {
					display: true,
					labelString: "Sample",
					fontColor: "red"
				}
			}],
			yAxes: [{
				// ticks: {
				// 	min: 0,
				// 	max: 10,
				// 	stepSize: 1
				// },
				scaleLabel: {
					display: true,
					labelString: "Distance [ Meter ]",
					fontColor: "red"
				}
			}]
		},
		elements: {
			line: {
				fill: false
			}
		},
		tooltips: {
			enabled: false,
		},
		plugins: {
			datalabels: {
				labels: {
					title: null
				}
			}
		}

	};
	public lineChartColors: Color[] = [];
	public lineChartLegend = false;
	public lineChartType = 'line';

	private sample_counter: SampleCounter[];

	private number_of_points_to_display: number;
	private average_each_n_point: number;
	private scale_min: number;
	private scale_max: number;
	private scale_step: number;

	private configurationServiceOnConfigurationChangeSubscribeObj: any;
	private continuousDataIsConnectedSubscribeObj: any;
	private rtlsUtilServiceInitializeDoneSubscribeObj: any;
	private rtlsUtilServiceDeviceListSubscribeObj: any;
	private rtlsUtilServiceTofEnabledSubscribeObj: any;

	private tof_enabled = false;
	private auto_scale_enabled = false;

	constructor(private configurationService: ConfigurationService,
		private continuousData: ContinuousDataService,
		private rtlsUtilService: RtlsUtilService,
		public dialog: MatDialog) { }

	ngOnInit() {
		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService.onConfigurationChange().subscribe(new_configuration => {
			this.number_of_points_to_display = new_configuration.general.tof_graph_parameters.number_of_points_to_display;
			this.average_each_n_point = new_configuration.general.tof_graph_parameters.average_each_n_point - 1;

			this.scale_min = new_configuration.general.tof_graph_parameters.scale.min;
			this.scale_max = new_configuration.general.tof_graph_parameters.scale.max;
			this.scale_step = new_configuration.general.tof_graph_parameters.scale.step;

			this.update_scale();
		});

		this.continuousDataIsConnectedSubscribeObj = this.continuousData.isConnected().subscribe(event => {
			if (event == "connected") {
				console.log("ToF chart subscribe to flow data")
				this.continuousData.data.subscribe(recv_data => {
					if (recv_data && recv_data.message.command === "RTLS_CMD_TOF_RESULT_DIST") {
						setTimeout(() => {
							this.updateChart2(recv_data);
						}, 20);
					}
				},
					error => {
						console.log(error);
					}
				);
			}
		});

		this.rtlsUtilServiceInitializeDoneSubscribeObj = this.rtlsUtilService.initialize_done.subscribe(() => {
			this.initChart();
		});

		this.rtlsUtilServiceDeviceListSubscribeObj = this.rtlsUtilService.deviceList.subscribe(deviceList => {
			this._deviceList = deviceList;

			this.initChart();
		});


		this.rtlsUtilServiceTofEnabledSubscribeObj = this.rtlsUtilService.tofEnabled.subscribe(enabled => {
			if (enabled) {
				this.initChart();
			}

			this.tof_enabled = enabled;
		});
	}

	ngOnDestroy() {
		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();
		this.continuousDataIsConnectedSubscribeObj.unsubscribe();
		this.rtlsUtilServiceInitializeDoneSubscribeObj.unsubscribe();
		this.rtlsUtilServiceDeviceListSubscribeObj.unsubscribe();
		this.rtlsUtilServiceTofEnabledSubscribeObj.unsubscribe();
	}

	initChart() {
		let selectedDeviceList = this._deviceList.filter(d => d.selected);

		this.lineChartLabels = Array<string>(this.number_of_points_to_display).fill("").map((v, i) => i.toString());

		if (selectedDeviceList.length > 0) {
			this.lineChartData = Array.from(selectedDeviceList, (d: SelectedDevice) => {
				return {
					data: [],
					label: d.bd_address,
				}
			});

			this.lineChartColors = Array.from(selectedDeviceList, (d: SelectedDevice) => {
				return { borderColor: d.graph_color };
			});

			this.sample_counter = Array.from(selectedDeviceList, (d: SelectedDevice) => {
				return {
					counter: 0,
					sample_sum: 0,
				}
			});
		} else {
			this.lineChartData = [];
			this.lineChartColors = [];
		}

		if (!this.auto_scale_enabled) {
			this.lineChartOptions.scales.yAxes[0].ticks = {
				min: this.scale_min,
				max: this.scale_max,
				stepSize: this.scale_step
			}
		}
	}

	updateChart2(recv_data) {
		let chatDataIndex = this.lineChartData.findIndex(x => x.label === recv_data.identifier);
		var chartData = this.lineChartData[chatDataIndex];
		let distance = recv_data.message.payload.distance;

		if (this.sample_counter[chatDataIndex].counter === this.average_each_n_point) {
			if ((chartData.data.length + 1) >= this.lineChartLabels.length) {
				chartData.data.shift();
			}

			let avg_distance = this.sample_counter[chatDataIndex].sample_sum / this.sample_counter[chatDataIndex].counter;
			avg_distance = this.average_each_n_point === 0 ? distance : avg_distance;

			chartData.data.push(avg_distance);

			recv_data.message.payload.distance = avg_distance;
			this.rtlsUtilService.logMessages.next(recv_data);

			this.lineChartData = this.lineChartData.slice();

			this.sample_counter[chatDataIndex].counter = 0;
			this.sample_counter[chatDataIndex].sample_sum = 0;
		} else {
			this.sample_counter[chatDataIndex].counter += 1;
			this.sample_counter[chatDataIndex].sample_sum += distance;
		}
	}

	downloadCanvas(event) {
		var canvas = document.getElementById('tof-canvas') as HTMLCanvasElement;

		var context = canvas.getContext('2d');

		//cache height and width
		var w = canvas.width;
		var h = canvas.height;

		var data = context.getImageData(0, 0, w, h);

		var compositeOperation = context.globalCompositeOperation;

		context.globalCompositeOperation = "destination-over";
		context.fillStyle = "#fff";
		context.fillRect(0, 0, w, h);

		var imageData = canvas.toDataURL("image/jpeg");

		context.clearRect(0, 0, w, h);
		context.putImageData(data, 0, 0);
		context.globalCompositeOperation = compositeOperation;

		var a = document.createElement('a');
		a.href = imageData;
		a.download = 'tof.jpeg';
		a.click();
	}

	showTofConfiguration() {
		const dialogRef = this.dialog.open(ConfigurationDialogComponent, {
			width: '900px',
			height: 'auto',
			disableClose: true,
			data: {
				configuration: this.configurationService.getCurrentConfiguration(),
				selected_tab: 3
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log('The dialog was closed', result);
			if (result !== undefined) {
				this.configurationService.setConfiguration(result);
			}
		});
	}

	auto_scale() {
		this.auto_scale_enabled = !this.auto_scale_enabled;

		this.update_scale();
	}

	update_scale() {
		if (this.chart) {
			if (this.auto_scale_enabled) {
				delete this.chart.chart.options.scales.yAxes[0].ticks
			} else {
				this.chart.chart.options.scales.yAxes[0].ticks = {
					min: this.scale_min,
					max: this.scale_max,
					stepSize: this.scale_step
				}
			}

			this.chart.chart.update();
			this.chart.chart.render();
		}
	}

}
