import { Component, OnInit, Inject, ViewEncapsulation, AfterViewInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Configuration } from 'app/interfaces/configuration';
import { ConfigurationService } from 'app/services/configuration.service';
import { FormControl, Validators } from '@angular/forms';


@Component({
	selector: 'configuration-dialog',
	templateUrl: './configuration-dialog.component.html',
	styleUrls: ['./configuration-dialog.component.scss'],
	encapsulation: ViewEncapsulation.None
})
export class ConfigurationDialogComponent implements OnInit {

	cciChannels = Array<string>(37).fill("").map((v, i) => i.toString());

	tofSampleModes = ["TOF_MODE_DIST", "TOF_MODE_STAT", "TOF_MODE_RAW"];
	tofRunModes = ["TOF_MODE_CONT", "TOF_MODE_AUTO"];
	freqList = [2408, 2412, 2416, 2418, 2420, 2424, 2430, 2432, 2436, 2438];

	aoaRunModes = ["AOA_MODE_ANGLE", "AOA_MODE_PAIR_ANGLES", "AOA_MODE_RAW"];
	antList = [0, 1, 2, 3, 4, 5]

	aoaSamplingFilters = [{ key: "Default Filtering", value: 0 }, { key: "RAW_RF No Filtering", value: 1 }];
	cc26x2SelectedAoaSamplingFilter = 0;
	cc2640r2SelectedAoaSamplingFilter = 0;

	aoaSamplingAntennas = [{ key: "Both", value: 0 }, { key: "Ant 1 Only", value: 16 }, { key: "Ant 2 Only", value: 32 }];
	cc26x2SelectedAoaSamplingAntenna = 16;
	cc2640r2SelectedAoaSamplingAntenna = 16;

	numberOfMessages = [50, 100, 150]

	configuration: Configuration;
	selectedTab: number = 0;
	disableAll: boolean = false;

	tofGraphAverageEachNPointFormControl = new FormControl('', Validators.min(1));
	tofGraphNumberOfPointsToDisplayFormControl = new FormControl('', Validators.min(25));

	aoaGraphAverageEachNPointFormControl = new FormControl('', Validators.min(1));
	aoaGraphNumberOfPointsToDisplayFormControl = new FormControl('', Validators.min(25));


	constructor(public dialogRef: MatDialogRef<ConfigurationDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private configurationService: ConfigurationService) { }

	onNoClick(): void {
		this.dialogRef.close();
	}

	onSave() {
		if (
			!(
				this.aoaGraphAverageEachNPointFormControl.hasError('min')
				|| this.aoaGraphNumberOfPointsToDisplayFormControl.hasError('min')
				|| this.tofGraphAverageEachNPointFormControl.hasError('min')
				|| this.tofGraphNumberOfPointsToDisplayFormControl.hasError('min')
			)
		) {
			this.configuration.general.tof_graph_parameters.number_of_points_to_display = this.tofGraphNumberOfPointsToDisplayFormControl.value;
			this.configuration.general.tof_graph_parameters.average_each_n_point = this.tofGraphAverageEachNPointFormControl.value;

			this.configuration.general.aoa_graph_parameters.number_of_points_to_display = this.aoaGraphNumberOfPointsToDisplayFormControl.value;
			this.configuration.general.aoa_graph_parameters.average_each_n_point = this.aoaGraphAverageEachNPointFormControl.value;

			this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control = this.cc26x2SelectedAoaSamplingFilter | this.cc26x2SelectedAoaSamplingAntenna;
			this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control = this.cc2640r2SelectedAoaSamplingFilter | this.cc2640r2SelectedAoaSamplingAntenna;

			this.dialogRef.close(this.configuration);
		}

	}

	ngOnInit() {
		this.configuration = this.data.configuration;
		this.selectedTab = this.data.selected_tab;
		this.disableAll = this.data.selected_tab !== -1 ? true : false;

		this.tofGraphNumberOfPointsToDisplayFormControl.setValue(this.configuration.general.tof_graph_parameters.number_of_points_to_display);
		this.tofGraphAverageEachNPointFormControl.setValue(this.configuration.general.tof_graph_parameters.average_each_n_point);

		this.aoaGraphNumberOfPointsToDisplayFormControl.setValue(this.configuration.general.aoa_graph_parameters.number_of_points_to_display);
		this.aoaGraphAverageEachNPointFormControl.setValue(this.configuration.general.aoa_graph_parameters.average_each_n_point);

		this.cc26x2SelectedAoaSamplingFilter = this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control & 0x0f;
		this.cc26x2SelectedAoaSamplingAntenna = this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control & 0xf0;
		this.cc2640r2SelectedAoaSamplingFilter = this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control & 0x0f;
		this.cc2640r2SelectedAoaSamplingAntenna = this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control & 0xf0;
	}

	select_channels(start: number, stop: number) {
		if (start !== -1 && stop !== -1) {
			this.configuration.cci_parameters.channels = Array<string>(stop - start + 1).fill("").map((v, i) => (i + start).toString());
		} else {
			this.configuration.cci_parameters.channels = [];
		}
	}

	set_default() {
		this.configuration = JSON.parse(JSON.stringify(this.configurationService.getDefaultConfiguration()));
		// this.configuration = this.configurationService.getDefaultConfiguration();

		this.cc26x2SelectedAoaSamplingFilter = this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control & 0x0f;
		this.cc26x2SelectedAoaSamplingAntenna = this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control & 0xf0;
		this.cc2640r2SelectedAoaSamplingFilter = this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control & 0x0f;
		this.cc2640r2SelectedAoaSamplingAntenna = this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control & 0xf0;

		this.tofGraphNumberOfPointsToDisplayFormControl.setValue(this.configuration.general.tof_graph_parameters.number_of_points_to_display);
		this.tofGraphAverageEachNPointFormControl.setValue(this.configuration.general.tof_graph_parameters.average_each_n_point);

		this.aoaGraphNumberOfPointsToDisplayFormControl.setValue(this.configuration.general.aoa_graph_parameters.number_of_points_to_display);
		this.aoaGraphAverageEachNPointFormControl.setValue(this.configuration.general.aoa_graph_parameters.average_each_n_point);
	}

	XOR(a: boolean, b: boolean): boolean {
		return (a || b) && !(a && b);
	}
}
