import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { MatDialog } from '@angular/material';
import { ConfigurationDialogComponent } from 'app/dialogs/configuration-dialog/configuration-dialog.component';
import { Configuration } from 'app/interfaces/configuration';
import { ConfigurationService } from 'app/services/configuration.service';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { ToastrService } from 'ngx-toastr';
import { ErrorDialogComponent } from 'app/dialogs/error-dialog/error-dialog.component';
import { ErrorType } from 'app/enums/error-type.enum';
import { error } from 'util';
import { SelectedDevice } from 'app/interfaces/selected-device';


@Component({
	selector: 'app-dashboard',
	templateUrl: './dashboard.component.html',
	styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit, OnDestroy, AfterViewInit {

	selected_devices: SelectedDevice[] = [];

	spinner_message = "Loading ..."

	_configuration: Configuration;

	is_general_error: boolean = false;

	is_one_of_features_is_active: boolean = false;
	features_state = {
		cciEnabled: false,
		tofEnabled: false,
		aoaEnabled: false,
	}

	configurationServiceOnConfigurationChangeSubscribeObj: any;

	rtlsUtilServiceErrorOccurSubscribeObj: any;
	rtlsUtilServiceDeviceListSubscribeObj: any;
	rtlsUtilServiceMsgForSpinnerSubscribe: any;
	rtlsUtilServiceCciEnabledSubscribeObj: any;
	rtlsUtilServiceTofEnabledSubscribeObj: any;
	rtlsUtilServiceAoaEnabledSubscribeObj: any;

	constructor(private spinner: NgxSpinnerService,
		public dialog: MatDialog,
		private configurationService: ConfigurationService,
		private rtlsUtilService: RtlsUtilService) { }

	async ngOnInit() {
		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService.onConfigurationChange().subscribe(new_configuration => {
			this._configuration = new_configuration;
		});

		this.rtlsUtilServiceErrorOccurSubscribeObj = this.rtlsUtilService.errorOccur.subscribe(errorType => {
			console.log(errorType);

			this.spinner.hide();
			this.is_general_error = (errorType === ErrorType.WebSocketError);

			const dialogRef = this.dialog.open(ErrorDialogComponent, {
				width: '500px',
				height: 'auto',
				disableClose: true,
				data: { 'error_type': errorType }
			});
		})

		this.rtlsUtilServiceDeviceListSubscribeObj = this.rtlsUtilService.deviceList.subscribe(deviceList => {
			this.selected_devices = deviceList;
			this.is_general_error = false;
		});

		this.rtlsUtilServiceMsgForSpinnerSubscribe = this.rtlsUtilService.msgForSpinner.subscribe(msg => {
			if (msg) {
				this.spinner_message = msg;
				this.spinner.show();
			} else {
				this.spinner.hide();
			}
		});

		this.rtlsUtilServiceCciEnabledSubscribeObj = this.rtlsUtilService.cciEnabled.subscribe(cciEnabled => { this.features_state.cciEnabled = cciEnabled; this.enable_disable_configurations(); });
		this.rtlsUtilServiceTofEnabledSubscribeObj = this.rtlsUtilService.tofEnabled.subscribe(tofEnabled => { this.features_state.tofEnabled = tofEnabled; this.enable_disable_configurations(); });
		this.rtlsUtilServiceAoaEnabledSubscribeObj = this.rtlsUtilService.aoaEnabled.subscribe(aoaEnabled => { this.features_state.aoaEnabled = aoaEnabled; this.enable_disable_configurations(); });

		let device_list = JSON.parse(localStorage.getItem('selectedDevicesList'));
		await this.rtlsUtilService.initialize(device_list, this._configuration.general.websocket_port, this._configuration.general.rtls_util_timeout);
		await this.rtlsUtilService.get_setup_capability();
	}

	ngAfterViewInit() {
	}

	ngOnDestroy() {
		this.rtlsUtilService.finalize().then(() => {
			this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();

			this.rtlsUtilServiceErrorOccurSubscribeObj.unsubscribe();
			this.rtlsUtilServiceDeviceListSubscribeObj.unsubscribe();
			this.rtlsUtilServiceMsgForSpinnerSubscribe.unsubscribe();
			this.rtlsUtilServiceCciEnabledSubscribeObj.unsubscribe();
			this.rtlsUtilServiceTofEnabledSubscribeObj.unsubscribe();
			this.rtlsUtilServiceAoaEnabledSubscribeObj.unsubscribe();
		});
	}

	auto_start() {
		if (!this.is_general_error) {
			console.log("auto_start");

			let device_list = JSON.parse(localStorage.getItem('selectedDevicesList'));
			this.rtlsUtilService.auto_play(
				device_list,
				this._configuration.general.websocket_port,
				this._configuration.general.rtls_util_timeout,
				this._configuration.scan_connection.scan_time,
				this._configuration.scan_connection.connection_interval,
				this._configuration.tof_parameters,
				this._configuration.aoa_parameters
			);
		}
	}

	restart() {
		let device_list = JSON.parse(localStorage.getItem('selectedDevicesList'));

		this.rtlsUtilService.restart(device_list, this._configuration.general.websocket_port, this._configuration.general.rtls_util_timeout);
	}

	enable_disable_configurations() {
		this.is_one_of_features_is_active = (this.features_state.cciEnabled || this.features_state.tofEnabled || this.features_state.aoaEnabled);
	}

	configurations() {
		if (!this.is_one_of_features_is_active) {
			let last_configuration: Configuration = JSON.parse(JSON.stringify(this.configurationService.getCurrentConfiguration()));

			const dialogRef = this.dialog.open(ConfigurationDialogComponent, {
				width: '900px',
				height: 'auto',
				disableClose: true,
				data: {
					configuration: JSON.parse(JSON.stringify(this.configurationService.getCurrentConfiguration())),
					selected_tab: -1
				}
			});

			dialogRef.afterClosed().subscribe(result => {
				console.log('The dialog was closed', result);
				if (result !== undefined) {
					if (last_configuration.general.show_tof_aoa_in_space_view !== result.general.show_tof_aoa_in_space_view) {
						this.restart();
					}

					setTimeout(() => {
						this.configurationService.setConfiguration(result);
					}, 1000);
				}
			});
		}
	}
}
