import { Component, OnInit, Input, ViewChild, AfterViewInit, OnDestroy, ViewEncapsulation } from '@angular/core';
import { MatTableDataSource, MatSort, MatSortable } from '@angular/material';
import { Slave } from 'app/interfaces/slave';
import { SelectionModel } from '@angular/cdk/collections';
import { RestApiService } from 'app/services/rest-api.service';
import { Configuration } from 'app/interfaces/configuration';
import { ConfigurationService } from 'app/services/configuration.service';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { SetupCapability } from 'app/interfaces/setup-capability';
import { ConnectionStatus } from 'app/interfaces/connection-status';

@Component({
	selector: 'slave-list',
	templateUrl: './slave-list.component.html',
	styleUrls: ['./slave-list.component.scss'],
	// encapsulation: ViewEncapsulation.None,
})
export class SlaveListComponent implements OnInit, OnDestroy {

	@Input() error_occur: boolean = false;

	@ViewChild(MatSort, { static: true }) sort: MatSort;

	private _configuration: Configuration;

	private _setup_capability: SetupCapability = {
		cci_supported: false,
		tof_supported: false,
		aoa_supported: false,
	};

	slave_connected = false;
	slave_connected_index = -1;
	selected_slave = null;

	cci_enabled = false;
	tof_enabled = false;
	aoa_enabled = false;


	displayedColumns: string[] = ['select', 'bd_address', 'rtls_role', 'rssi'];
	dataSource = new MatTableDataSource<Slave>([]);
	selection = new SelectionModel<Slave>(false, []);

	configurationServiceOnConfigurationChangeSubscribeObj: any;
	rtlsUtilServiceSlaveListSubscribeObj: any;
	rtlsUtilServiceSetupCapabilitySubscribeObj: any;
	rtlsUtilServiceConnectionStatusSubscribeObj: any;
	rtlsUtilServiceCciEnabledSubscribeObj: any;
	rtlsUtilServiceTofEnabledSubscribeObj: any;
	rtlsUtilServiceAoaEnabledSubscribeObj: any;


	constructor(private configurationService: ConfigurationService, private rtlsUtilService: RtlsUtilService) { }

	ngOnInit() {
		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService.onConfigurationChange().subscribe(configuration => {
			this._configuration = configuration;
			console.log("Configuration updated in slave component");
		})

		this.rtlsUtilServiceSlaveListSubscribeObj = this.rtlsUtilService.slaveList.subscribe(slaveList => {
			this.dataSource = new MatTableDataSource<Slave>(slaveList);

			this.dataSource.sort = this.sort;
			this.dataSource.sortingDataAccessor = (data, attribute) => {
				switch (attribute) {
					case 'rssi':
						return Math.abs(data[attribute]);
					default:
						return data[attribute];
				}
			};

			this.dataSource.sort.sort(<MatSortable>{
				id: 'rssi',
				start: 'asc'
			})
		});

		this.rtlsUtilServiceSetupCapabilitySubscribeObj = this.rtlsUtilService.setupCapability.subscribe(setupCapability => {
			this._setup_capability = setupCapability;
			this.slave_connected = false;
		});

		this.rtlsUtilServiceConnectionStatusSubscribeObj = this.rtlsUtilService.onConnectionStatusChange().subscribe(slaveConneted => this.update_connection_status(slaveConneted));
		this.rtlsUtilServiceCciEnabledSubscribeObj = this.rtlsUtilService.cciEnabled.subscribe(cciEnabled => this.cci_enabled = cciEnabled);
		this.rtlsUtilServiceTofEnabledSubscribeObj = this.rtlsUtilService.tofEnabled.subscribe(tofEnabled => this.tof_enabled = tofEnabled);
		this.rtlsUtilServiceAoaEnabledSubscribeObj = this.rtlsUtilService.aoaEnabled.subscribe(aoaEnabled => this.aoa_enabled = aoaEnabled);
	}

	ngOnDestroy() {
		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();
		this.rtlsUtilServiceSlaveListSubscribeObj.unsubscribe();
		this.rtlsUtilServiceSetupCapabilitySubscribeObj.unsubscribe();
		this.rtlsUtilServiceConnectionStatusSubscribeObj.unsubscribe();
		this.rtlsUtilServiceCciEnabledSubscribeObj.unsubscribe();
		this.rtlsUtilServiceTofEnabledSubscribeObj.unsubscribe();
		this.rtlsUtilServiceAoaEnabledSubscribeObj.unsubscribe();
	}


	sortChange(e) {
		if (e.direction !== "") {
			this.dataSource.sortData(this.dataSource.filteredData, this.dataSource.sort);
		}
	}

	update_connection_status(connectino_status: ConnectionStatus) {
		// console.log("update_connection_status", connectino_status);

		this.slave_connected = connectino_status.success;
		this.slave_connected_index = -1;

		if (connectino_status.slave !== null) {
			this.selection.select(connectino_status.slave);

			this.slave_connected_index = this.dataSource.filteredData.indexOf(connectino_status.slave);
		} else {
			this.selection.clear();
		}

	}

	scan_for_slave() {
		this.rtlsUtilService.scan(this._configuration.scan_connection.scan_time);
	}

	connect_to_slave() {
		this.selected_slave = this.selection.selected[0];

		this.rtlsUtilService.connect(this.selected_slave, this._configuration.scan_connection.connection_interval);
	}

	disconnect_from_slave() {
		this.rtlsUtilService.disconnect();
	}

	enable_cci() {
		this.rtlsUtilService.enable_cci();
	}

	disable_cci() {
		this.rtlsUtilService.disable_cci();
	}

	enable_tof() {
		this.rtlsUtilService.enable_tof(this._configuration.tof_parameters);
	}

	disable_tof() {
		this.rtlsUtilService.disable_tof();
	}

	enable_aoa() {
		this.rtlsUtilService.enable_aoa(this._configuration.aoa_parameters);
	}

	disable_aoa() {
		this.rtlsUtilService.disable_aoa();
	}
}
