import { Injectable } from '@angular/core';
import { Configuration } from 'app/interfaces/configuration';
import { BehaviorSubject } from 'rxjs';
import { RestApiService } from './rest-api.service';

@Injectable({
	providedIn: 'root'
})
export class ConfigurationService {

	private default_configuration: Configuration = {
		general: {
			websocket_port: 5050,
			rtls_util_timeout: 30,
			max_log_messages: 10000,
			number_of_messages_per_page: 100,
			show_tof_aoa_in_space_view: false,
			cci_graph_parameters: {
				scale: {
					min: 0,
					max: 110,
					step: 10
				}
			},
			tof_graph_parameters: {
				number_of_points_to_display: 50,
				average_each_n_point: 2,
				scale: {
					min: -5,
					max: 5,
					step: 1
				}
			},
			aoa_graph_parameters: {
				number_of_points_to_display: 50,
				average_each_n_point: 2,
				scale: {
					min: -180,
					max: 180,
					step: 20
				}
			},
			space_view_parameters: {
				points_per_meter: 100
			}
		},
		scan_connection: {
			scan_time: 10,
			connection_interval: 100
		},
		tof_parameters: {
			tof_sample_mode: "TOF_MODE_DIST",
			tof_run_mode: "TOF_MODE_CONT",
			tof_auto_rssi: -55,
			tof_slave_lqi_filter: 25,
			tof_post_process_lqi_thresh: 20,
			tof_post_process_magn_ratio: 111,
			tof_samples_per_burst: 256,
			tof_freq_list: [2416, 2418, 2420, 2424, 2430, 2432, 2436, 2438],
			calibration_params: {
				samples_per_freq: 1000,
				distance: 1,
				use_nv_calib: false
			}
		},
		aoa_parameters: {
			aoa_run_mode: "AOA_MODE_ANGLE",
			aoa_cc2640r2: {
				aoa_cte_scan_ovs: 4,
				aoa_cte_offset: 4,
				aoa_cte_length: 20,
				aoa_sampling_control: 0
			},
			aoa_cc26x2: {
				aoa_cte_length: 20,
				aoa_cte_interval: 1,
				aoa_slot_durations: 1,
				aoa_sample_rate: 1,
				aoa_sample_size: 1,
				aoa_sampling_control: 16,
				aoa_sampling_enable: 1,
				aoa_pattern_len: 3,
				aoa_ant_pattern: [0, 1, 2]
			}

		},
		cci_parameters: {
			channels: ['1', '5', '10', '15', '20', '25', '30']
		}
	}

	private _configuration: BehaviorSubject<Configuration> = new BehaviorSubject<Configuration>(this.default_configuration);

	constructor(private restApiService: RestApiService) {
		restApiService.getConfiguration().subscribe(server_configuration => {
			if (server_configuration) {
				this._configuration.next(server_configuration);
			} else {
				this._configuration.next(this.default_configuration);
			}
		});
	}

	onConfigurationChange() {
		return this._configuration;
	}

	getCurrentConfiguration(): Configuration {
		return this._configuration.getValue();
	}

	getDefaultConfiguration(): Configuration {
		return this.default_configuration;
	}

	setConfiguration(v: Configuration) {
		this._configuration.next(v);
		this.restApiService.setConfiguration(v).subscribe(() => { });
	}

}
